:root {
  --font-montserrat-regular: "Montserrat-Regular", sans-serif;
  --font-montserrat-bold: "Montserrat-Bold", sans-serif;
  --font-mulish-bold: "Mulish-Bold", sans-serif;
  --font-mulish-semibold: "Mulish-SemiBold", sans-serif;
  --content-width: 1720px;
  --container-offset: 100px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --dark-13: #131313;
  --dark-17: #171717;
  --dark: #666666;
  --brown-81: #C2AB81;
  --grey-80: #808080;
  --10-14: clamp(0.625rem, 0.4565rem + 0.3524vw, 0.875rem);
  --20-36: clamp(1.25rem, 0.5341rem + 3.5794vw, 2.25rem);
  --12-16: clamp(0.75rem, 0.3214rem + 0.5714vw, 1rem);
  --10-14: clamp(0.625rem, 0.1964rem + 0.5714vw, 0.875rem);
}

