@font-face {
  font-family: "Montserrat-Regular";
  src: url("../fonts/../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat-Bold";
  src: url("../fonts/../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Mulish-Bold";
  src: url("../fonts/../fonts/Mulish-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Mulish-SemiBold";
  src: url("../fonts/../fonts/Mulish-SemiBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  background-color: var(--dark-13);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

/* header */
.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 28px;
  /* burger */
  /* end burger */
}
@media (max-width: 1658px) {
  .header-content {
    flex-wrap: wrap;
  }
}
.header-content__logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 76px;
  color: var(--light-color);
  transition: color ease-in-out 0.5s;
}
.header-content__logo:hover {
  color: var(--brown-81);
}
@media (max-width: 1658px) {
  .header-content__logo {
    order: 1;
  }
}
.header-content__burger {
  position: relative;
  display: none;
  z-index: 1;
}
@media (max-width: 992px) {
  .header-content__burger {
    display: flex;
    align-items: center;
  }
}
.header-content__nav {
  position: relative;
  margin-right: auto;
}
@media (max-width: 1658px) {
  .header-content__nav {
    margin-right: 0;
    order: 3;
  }
}
@media (max-width: 992px) {
  .header-content__nav {
    display: none;
  }
  .header-content__nav.header-content__active {
    position: absolute;
    left: 0;
    display: flex;
    z-index: 10;
  }
}
@media (max-width: 992px) {
  .header-content__active .header-content__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
  }
}
.header-content__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 42px;
  align-items: center;
  justify-content: space-between;
  margin: 0;
}
@media (max-width: 992px) {
  .header-content__list {
    width: 100vw;
    padding-left: var(--container-offset);
    background-color: var(--dark-13);
  }
}
.header-content__link {
  font-family: var(--font-montserrat-bold);
  font-size: var(--10-14);
  line-height: 150%;
  font-weight: 700;
  color: var(--light-color);
  text-transform: uppercase;
  transition: text-shadow ease-in-out 0.5s;
}
.header-content__link:hover, .header-content__link:focus {
  text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px var(--brown-81), 0 0 30px var(--brown-81), 0 0 40px var(--brown-81), 0 0 55px var(--brown-81), 0 0 75px var(--brown-81);
}
@media (max-width: 992px) {
  .header-content__link {
    font-size: var(--20-36);
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: var(--grey-80);
    color: transparent;
  }
}
.header-content__actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1658px) {
  .header-content__actions {
    order: 2;
  }
}
@keyframes shaking {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(2deg);
  }
  50% {
    transform: rotate(0eg);
  }
  75% {
    transform: rotate(-2deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.header-content__phone {
  display: block;
  padding: 20px 20px 20px 20px;
  color: var(--light-color);
  transition: color ease-in-out 0.5s;
}
.header-content__phone:hover {
  animation: shaking 0.25s infinite;
  color: var(--brown-81);
}
.header-content__button {
  position: relative;
  display: inline-flex;
  padding: 21px 78px 21px 78px;
  font-family: var(--font-montserrat-bold);
  font-size: 12px;
  line-height: 150%;
  font-weight: 700;
  color: var(--light-color);
  text-transform: uppercase;
  background-color: var(--brown-81);
  border: none;
  transform: skewX(-18deg);
  transition: color ease-in-out 0.5s, background-color ease-in-out 0.5s;
  cursor: pointer;
}
.header-content__button:hover, .header-content__button:focus {
  color: var(--brown-81);
  background-color: var(--light-color);
}
.header-content__button:hover::after, .header-content__button:focus::after {
  background-color: var(--brown-81);
  transition: backgorund-color ease-in-out 0.5s;
}
.header-content__button::after {
  position: absolute;
  content: "";
  top: 0;
  right: -20px;
  bottom: 0;
  width: 10px;
  background-color: var(--light-color);
}
.header-content__button span {
  display: block;
  transform: skewX(18deg);
}

