/* posts */
.posts {
  margin-bottom: 200px;
}

.posts-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.posts-content__title {
  margin: 0 0 53px 0;
  font-family: var(--font-montserrat-bold);
  font-size: 42px;
  line-height: 100%;
  font-weight: 700;
  color: var(--light-color);
  text-transform: uppercase;
}
.posts-content__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 30px;
  row-gap: 30px;
  margin: 0 0 60px 0;
  padding: 0;
}
.posts-content__item {
  width: calc((100% - 120px) / 5);
  background-color: var(--light-color);
}
@media (max-width: 1400px) {
  .posts-content__item {
    width: calc((100% - 90px) / 4);
